import React, { useState, useRef, useEffect } from "react";
import { Link, NavLink, useLocation } from "react-router-dom";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import light from "./Photos/light.png";
import img from "./Photos/logo.png";
import "./Navbar.css";

const Navbar = () => {
  const [activeNavItem, setActiveNavItem] = useState("");
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const sidebarRef = useRef(null);
  const location = useLocation();

  useEffect(() => {
    const handleTouchStart = (event) => {
      const target = event.target;
      if (sidebarRef.current && !sidebarRef.current.contains(target) && sidebarOpen) {
        setSidebarOpen(false);
      }
    };

    document.addEventListener("touchstart", handleTouchStart);

    return () => {
      document.removeEventListener("touchstart", handleTouchStart);
    };
  }, [sidebarOpen]);

  useEffect(() => {
    // Update activeNavItem based on the current location
    const path = location.pathname;
    if (path === "/") {
      setActiveNavItem("home");
    } else if (path === "/services") {
      setActiveNavItem("services");
    } else if (path === "/about") {
      setActiveNavItem("about");
    }
  }, [location]);

  const handleNavItemClick = (item) => {
    setActiveNavItem(item);
    setSidebarOpen(false); // Close the sidebar when a menu item is clicked
  };

  const handleSidebarToggle = () => {
    setSidebarOpen(!sidebarOpen);
    
  };

  const isContactPage = location.pathname === "/contact";

  return (
    <div>
      
      <nav className={`navbar navbar-expand-lg ${sidebarOpen ? "open" : ""}`}>
        <div className="container">
          <Link to="/" className="navbar-brand">
            <img src={img} alt="Logo" className="logo" />
          </Link>
          <button className="navbar-toggler" type="button" onClick={handleSidebarToggle}>
            <div className="dots"></div>
            <div className="dots"></div>
            <div className="dots"></div>
          </button>
          <div
            ref={sidebarRef}
            className={`collapse navbar-collapse ${sidebarOpen ? "show" : ""}`}
            id="navbarNav"
          >
            <ul className="navbar-nav ms-auto">
              {!isContactPage && (
                <li className={`nav-item ${activeNavItem === "home" ? "active" : ""}`}>
                  <NavLink to="/" className="nav-link" activeClassName="active" onClick={() => handleNavItemClick("home")}>
                    Home
                  </NavLink>
                </li>
              )}
              {!isContactPage && (
                <li className={`nav-item ${activeNavItem === "services" ? "active" : ""}`}>
                  <NavLink to="/services" className="nav-link" activeClassName="active" onClick={() => handleNavItemClick("services")}>
                    Services
                  </NavLink>
                </li>
              )}
              {!isContactPage && (
                <li className={`nav-item ${activeNavItem === "about" ? "active" : ""}`}>
                  <NavLink to="/about" className="nav-link" activeClassName="active" onClick={() => handleNavItemClick("about")}>
                    About
                  </NavLink>
                </li>
              )}
              {!isContactPage && (
                <li className="nav-item">
                  <Link to="/contact" className="nav-link contact" onClick={() => handleNavItemClick("")}>
                    Contact
                  </Link>
                </li>
              )}
            </ul>
          </div>
        </div>
      </nav>
      <img src={light} alt="Logo 2" className="light" />
    </div>
  );
};

export default Navbar;



