// export default AnimationRoutes

import { motion } from "framer-motion";

const opacityAnimationRoutes = {
    initial: {
        opacity:0,
    },
    animate:{
        opacity:1,
    },
    exit:{
        opacity: 0, 
    }
}

const AnimationRoutes = ({ children }) => {
  return (
    <motion.div
   transition={{
    ease:'easeInOut',
    duration:0.3
   }}

   variants={opacityAnimationRoutes}
   initial='initial'
   animate='animate'
   exit='exit'
   >
      {children}
    </motion.div>
  );
};

export default AnimationRoutes;
